import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import CardsData from './CardsData'
import "./style.css"
import {ADD} from '../redux/actions/Action'




const Cards = () => {
    const [data, setData] = useState(CardsData);
   // console.log(data);


   const dispatech = useDispatch();

   const send = (e) =>{
       //console.log(e);
       dispatech(ADD(e))

   }


    return (
        <div className='container my-3'>
            <h2 className='text-center'>Add to Cart Project</h2>
            <div className="row d-flex justify-content-center align-items-center">
                {
                    data.map((element, id) => {
                        return (
                            <>
                                <div className="card_style mx-2 mt-4 " style={{ width: "22rem", border:"none" }}>
                                    {/* <Cards.img variant="top" src="element.imgdata"></Cards.img> */}
                                    <img src={element.imgdata} className="card-img-top  mt-3" style={{height:"16rem"}} />
                                    <div className="card-body">
                                        <h5 className="card-title">{element.rname}</h5>
                                        <p className="card-text">
                                            Price Rs:  {element.price}
                                
                                            </p>
                                            <div className="button_div d-flex justify-content-center">

                                            <a  onClick={()=> send(element)} className="btn btn-primary col lg-12 ">Add to Cart</a>
                                            </div>
                                      
                                    </div>
                                </div>
                            </>
                        )
                    })
                }


            </div>
        </div>
    )
}

export default Cards
