import React, { useEffect, useState } from 'react'
import Badge from '@mui/material/Badge';
import Menu from '@mui/material/Menu';
import { NavLink } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Table } from 'react-bootstrap';
import { DLT } from '../redux/actions/Action';




const Header = () => {

    const [price , setPrice] = useState(0);
    console.log(price);

    const getdata = useSelector((state) => state.cartreducer.carts);
    console.log(getdata);

    const dispatch = useDispatch();


    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

    const dlt = (id)=>{
        dispatch(DLT(id))
    }

    const total = () =>{
    let  price =0;
    getdata.map((ele, k)=>{
        price = ele.price * ele.qnty + price;
    });
    setPrice(price);
    }
    useEffect(()=>{
        total();
    },[total])

    return (

        <>
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark" style={{ height: "60px" }}>
                <div className="container-fluid">
                    <NavLink className="navbar-brand ml-5" to="/">Add to Cart</NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                        <div className="navbar-nav">
                            <NavLink className="nav-link active" aria-current="page" to="/">Home</NavLink>

                        </div>

                    </div>
                    <Badge badgeContent={getdata.length} color="primary"
                        id="basic-button"
                        aria-controls={open ? 'basic-menu' : undefined}
                        aria-haspopup="true"
                        aria-expanded={open ? 'true' : undefined}
                        onClick={handleClick}
                    >
                        <i className="fa-solid fa-cart-shopping text-light " style={{ fontSize: 25, cursor: "pointer" }}></i>
                    </Badge>
                </div>
                <Menu
                    id="basic-menu"
                    anchorEl={anchorEl}
                    open={open}
                    onClose={handleClose}
                    MenuListProps={{
                        'aria-labelledby': 'basic-button',
                    }}
                >


                    {
                        getdata.length ?
                            <div className='cart_details' style={{ width: "24rem", Padding: 10 }}>
                                <Table>
                                    <thead>
                                        <tr>
                                            <th>Photos</th>
                                            <th>Restaurant Name</th>
                                        </tr>
                                    </thead>
                                    <tbody>{
                                        getdata.map((e)=>{
                                            return(
                                                <>
                                                <tr>
                                                    <td>
                                                    <NavLink to={`/cart/${e.id}`} onClick={handleClose}><img src={e.imgdata} style ={{width: "5rem", height: "5rem"}} ></img></NavLink>
                                                    </td>
                                                    <td>
                                                        <p>
                                                            <strong>{e.rname} </strong>
                                                           
                                                        </p>
                                                        <p>
                                                            Price Rs: {e.price}
                                                        </p>
                                                        <p>
                                                            Quantity: {e.qnty}
                                                        </p> <p style={{color: "red", fontSize: 20, cursor:"pointer"}} onClick={()=>dlt(e.id)}>
                                                            <i className='fas fa-trash smalltrash'></i></p>
                                                    </td>
                                                    <td className='mt-5' style={{color: "red", fontSize: 20, cursor:"pointer"}} onClick={()=>dlt(e.id)}>
                                                        <i className='fas fa-trash largetrash'></i>

                                                    </td>
                                                </tr>
                                                </>
  
                                            )
 
                                        })
                                    }

                                    <p className='text-center'>Total Rs: {price}</p>
                                    </tbody>
                                </Table>
                            </div> :

                            <div className='cart_details d-flex justify-content-center align-items-center ' style={{ width: "24rem", padding: 10, postition: "relative" }}>
                                <i className='fas fa-close smallclose' onClick={handleClose}
                                    style={{ position: "absolute", top: 2, right: 20, fontSize: 23, cursor: "pointer" }}></i>
                                <p style={{ fontSize: 22 }}>Your Carts is Empty!</p>
                                <img src="./cart.gif" alt="" className='emptycart_img' style={{ width: "5rem", padding: 10 }}></img>
                            </div>
                    }






                </Menu>
            </nav>
        </>


    )
}

export default Header
